<template>
  <div id="quiz">
    <head-top title="随堂测验"></head-top>
    <van-tabs v-model="headTopActive">
      <van-tab title="截屏提问">
        <van-pull-refresh v-model="isLoading" @refresh="getQuestionData" class="pull_fresh">
          <div class="question-content" v-if="questionList.length>0">
            <div class="item" @click="selectQuestion(item)" v-for="(item, k) in questionList" :key="k">
              <div class="bg fL">
                <img :src="item.img" alt="" />
              </div>
              <div class="details fR">
                <div class="info">
                  {{item.SSSerial + '.[' + getQuestionTypeName(item.SSType) + '] ' + item.SSScore + '分'}}
                </div>
                <div class="time">{{item['CreateTime'] | formatDate(item['CreateTime']) }}</div>
              </div>
              <span class="prompt" :class="item.SSPattern=== 3 ? 'red':'green'">{{getPatternName(item.SSPattern)}}</span>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
        </van-pull-refresh>
      </van-tab>
      <van-tab title="随堂练习">
        <van-pull-refresh v-model="isLoading" @refresh="getPracticeData" class="pull_fresh">
          <div class="practice-content" v-if="practiceList.length>0">
            <div class="item" @click="selectPractice(item)" v-for="(item, index) in practiceList" :key="index">
              <div class="biaoti">
                {{item["PLTitle"]}}
                <div class="line_state" :class="['3', '4'].indexOf(item['CIPState'])>-1 ? 'red':(item['CIPState']==='2'?'green':'')">
                  {{getStateName(item['CIPState'])}}</div>
              </div>
              <div class="content">
                <div>
                  总分:
                  <span>{{item["Score"]}}</span>
                </div>
                <div>
                  题数:
                  <span>{{item["Total"]||0}}题</span>
                </div>
                <div>
                  时间:
                  <span>{{item.Key['CreateTime'] | formatDate(item.Key['CreateTime']) }}</span>
                </div>
              </div>
            </div>
          </div>
          <r-lack-data v-else></r-lack-data>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import {
  screenQuestionTypeName,
  screenPatternName,
  ClassPracticeStateName,
  ModulePicture,
  PracticeFunc
} from "@/utils/enum";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      headTopActive: 0,
      isLoading: false,
      questionList: [],
      practiceList: []
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      if (from.path.indexOf("/classPractice") > -1) {
        vm.headTopActive = 1;
      }
    });
  },
  mounted() {
    this.$nextTick(() => {
      if (this.$route.params && this.$route.params.selected === "1") {
        this.headTopActive = 1;
      }
      this.getQuestionData();
      this.getPracticeData();
    });
  },
  methods: {
    getQuestionData() {
      this.isLoading = true;
      this.questionList = [];
      this.$mainHttp.screenQuestion.getList({
        ciID: this.$store.getters.courseID,
        ssType: 0
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.questionList.push({
              ...item,
              img: this.$mainHttp.common.picture(
                item["SSContent"],
                ModulePicture.ScreenSubject
              )
            });
          });
          // 默认自带排序了
          // this.questionList.sort((a, b) => {
          //   return parseInt(a["SSSerial"]) - parseInt(b["SSSerial"]);
          // });
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    getPracticeData() {
      this.isLoading = true;
      this.practiceList = [];
      this.$mainHttp.classPractise.getList({
        ciID: this.$store.getters.courseID
      }).then(res => {
        this.practiceList = [];
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            if (this.practiceList.indexOf(item) === -1)
              this.practiceList.push(item);
          });
          // this.practiceList.sort(r => r["CreateTime"]).reverse();
        }
        this.isLoading = false;
      }).catch(err => {
        this.isLoading = false;
      });
    },
    getQuestionTypeName(val) {
      return screenQuestionTypeName(val);
    },
    getPatternName(val) {
      return screenPatternName(val);
    },
    getStateName(val) {
      return ClassPracticeStateName(parseInt(val));
    },
    selectQuestion(item) {
      this.$router.push("/screenQuestion/" + item["SSID"]);
    },
    selectPractice(item) {
      if (item["State"] === 1) {
        return;
      }
      this.$router.push(
        "/classPractice/" +
        item["CIPID"] +
        "/" +
        item.Key["PLID"] +
        "/" +
        item["Total"] +
        "/" +
        item["CIPState"]
      );
    }
  },
  watch: {
    "$store.getters.classPractice"(newVal, oldVal) {
      if (newVal !== oldVal) {
        console.log("newVal:", newVal);
        let isInit = newVal.step === 0;
        let isEnd =
          newVal.type === PracticeFunc.end && this.headTopActive === 1;
        if (isInit || isEnd) {
          console.log("111111111");
          this.headTopActive = 1;
          this.getPracticeData();
        }
      }
    }
  }
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");
#quiz {
  font-size: @font-info;
  .question-content {
    margin-top: 10px;
    width: 100%;
    padding-bottom: 2vh;
    background: #fff;
    .item {
      height: 12vh;
      width: 93vw;
      margin: 0 auto;
      overflow: hidden;
      position: relative;
      border-bottom: 1px solid rgba(221, 221, 221, 1);
      .bg {
        width: 30vw;
        height: 9vh;
        margin: 1vh;
        margin-left: 0px;
        text-align: center;
        img {
          max-width: 30vw;
          max-height: 9vh;
        }
      }
      .details {
        text-align: left;
        width: 53vw;
        height: 10vh;
        margin-top: 1vh;
        padding-left: 5vw;
      }
      .info {
        font-size: @font-sub-title;
        font-family: MicrosoftYaHei;
        color: rgba(51, 51, 51, 1);
        line-height: 1.5;
        margin-bottom: 3vh;
      }
      .time {
        font-size: @font-info;
        font-family: MicrosoftYaHei;
        color: rgba(102, 102, 102, 1);
      }
      .prompt {
        position: absolute;
        top: 1vh;
        right: 0;
      }
      .red {
        color: #e97b7b;
      }
      .green {
        color: #00cbab;
      }
    }
  }
  .practice-content {
    text-align: left;
    width: 100vw;
    // height: 34vh;
    padding-bottom: 2vh;
    margin-top: 2.2vh;

    background: rgba(255, 255, 255, 1);
    .item {
      overflow: hidden;
      width: 92vw;
      margin: 0 auto;
      border-bottom: 1px solid #ddd;
      .biaoti {
        font-size: @font-sub-title;
        line-height: 2.5;
        font-family: MicrosoftYaHei;
        color: #111;

        .line_state {
          text-align: right;
          float: right;
        }
        .red {
          color: #e97b7b;
        }
        .green {
          color: #00cbab;
        }
      }
      .content {
        font-size: @font-info;
        color: #888;
        line-height: 2.5;
        display: flex;
        justify-content: space-between;
      }
    }
  }

  .rightBtn {
    .van-button--primary {
      position: absolute;
      right: 15px;
      top: 15px;
      padding: 5px;
      font-size: @font-info;
      width: 50px;
      height: 20px;
      background: rgba(0, 203, 171, 1);
      line-height: 12px;
      border-radius: 3px;
      color: #fff;
      border: 1px solid #00cbab;
    }
    .van-button--info {
      position: absolute;
      right: 15px;
      top: 15px;
      padding: 5px;
      font-size: @font-info;
      width: 50px;
      height: 20px;
      background: blue;
      line-height: 12px;
      border-radius: 3px;
      color: #fff;
      border: 1px solid #00cbab;
    }
  }

  .van-tabs__wrap {
    position: fixed;
    width: 100%;
    top: 43px;
    z-index: 9;
    background: #ebf0ef;
    height: 59px;
    .van-tabs__nav--line {
      height: 44px;
      background: none;
    }
    .van-tab {
      background: #fff;
      &span {
        font-size: @font-info;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(0, 203, 171, 1);
      }
    }
    .van-tabs__line {
      width: 16vw;
      height: 1px;
      background: rgba(0, 203, 171, 1);
    }
  }
}
</style>


